.Header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--color-bg-border);
  padding: var(--space-xs) var(--space-l);
  margin-bottom: var(--space-s);
  &_leftSide {
    display: flex;
    flex-direction: row;
    align-items: center;
    &_logo {
      margin-right: var(--space-xs);
      height: 16px;
      position: relative;
      top: 1px;
    }
  }
  &_rightSide {
    display: flex;
    flex-direction: row;
    align-items: center;
    &_module {
      display: inherit;
      flex-direction: inherit;
      align-items: baseline;
      margin-right: var(--space-l);
      &:last-child {
        margin-right: 0px;
      }
    }
  }
}
